.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.black-nav {
  display : flex;
  background: black;
  width: 100%;
  color : white;
  padding-left: 20px;
}

.section {
  background: rgb(237, 235, 235);
  width: 100%;
  color : rgb(71, 68, 68);
  padding : 20px 0 20px ;
  margin-bottom: 5px;
}

.likes {
  background: rgb(237, 235, 235);
  color: #2b2b2c;
  width: auto;
  min-width: 80px; /* 최소 너비를 고정 */
  display: inline-block; /* 너비 고정을 위한 속성 */
  text-align: center; /* 텍스트 가운데 정렬 */
  border: 2px solid rgb(168, 162, 162);
  border-radius: 15px;
  padding: 3px;
}
.unlikes {
  background: rgb(68, 88, 160);
  color: #ffffff;
  width: auto;
  min-width: 80px; /* 최소 너비를 고정 */
  display: inline-block; /* 너비 고정을 위한 속성 */
  text-align: center; /* 텍스트 가운데 정렬 */
  border: 2px solid rgb(168, 162, 162);
  border-radius: 15px;
  padding: 3px;
}

.text-title{
  color : #000000;
}

.delete-btn {
  margin-right: 10px; /* 버튼과 제목 간의 간격 조정 */
  padding: 5px 10px;
  border: none;
  background-color: rgb(221, 221, 221);
  color: white;
  cursor: pointer;
}
.delete-btn:hover {
  margin-right: 10px; /* 버튼과 제목 간의 간격 조정 */
  padding: 5px 10px;
  border: none;
  background-color: rgb(107, 104, 104);
  color: white;
  cursor: pointer;
}
.content{
  font-size: 13px;
  color : black;
}


.container {
  display: flex;
  flex-direction: column; /* 수직으로 배치 */
  align-items: center; /* 가로 중앙 정렬 */
  justify-content: center; /* 세로 중앙 정렬 (필요한 경우 사용) */
}
.write {
  width: 100%;
  max-width: 800px; /* 텍스트 상자의 최대 너비 */
  height: 200px;
  padding: 15px;
  font-size: 16px;
  font-family: 'Arial', sans-serif;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
  color: #333;
  line-height: 1.5;
  resize: vertical; /* 높이만 조절 가능 */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* 약간의 그림자 효과 */
  outline: none;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.write:focus {
  border-color: #007bff; /* 포커스 시 테두리 색상 변경 */
  box-shadow: 0 4px 10px rgba(0, 123, 255, 0.2); /* 포커스 시 그림자 강화 */
}

.write::placeholder {
  color: #999; /* placeholder 텍스트 색상 */
  font-style: italic;
}

.submit-btn {
  display: inline-block;
  padding: 12px 24px;
  font-size: 16px;
  font-family: 'Arial', sans-serif;
  font-weight: bold;
  color: #fff;
  background-color: #007bff; /* 기본 버튼 색상 */
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.submit-btn:hover {
  background-color: #0056b3; /* 마우스 오버 시 색상 */
  transform: translateY(-2px); /* 약간의 움직임으로 클릭 가능성 강조 */
}

.submit-btn:active {
  background-color: #004080; /* 버튼 클릭 시 색상 */
  transform: translateY(0); /* 클릭 시 버튼이 내려오는 효과 */
}

.submit-btn:focus {
  outline: none; /* 포커스 시 외곽선 제거 */
  box-shadow: 0 0 8px rgba(0, 123, 255, 0.6); /* 포커스 시 강조 */
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
